/* eslint-disable no-alert */
import React from "react";
import { Paper, Divider, } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import { makeStyles , MuiThemeProvider } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import { storiesOf } from "@storybook/react";
import themes from "../../../Theme/themes";
import { BrandType } from "../../../types/brand";
import PesqincHotel from "../PesqincHotel";

const useStyles = makeStyles((theme) => ({
    root: {
        background: theme.palette.background.default,
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        boxShadow: theme.shadows[8],
        padding: "30px",
    },
}));

type PesqincHotelStoryProps = {
    mode: string,
    brand: BrandType
}

const PesqincHotelStory = ({
    mode, brand
}: PesqincHotelStoryProps) => {
    const classes = useStyles(themes[brand]);

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="Pesqinc/Hotel">
                <Typography variant="h3">PesqincHotel</Typography>
                <br />
                <Divider />
                <br />
                <Grid>
                    <PesqincHotel 
                        resetValue={() => alert("Clicado")}
                        onSelect={() => alert("Clicado")}
                        search={[
                            {
                                name: "São Paulo - São Paulo ",
                                address: "BR, Brasil, Todos os Aeroportos (SAO)",
                                type: "location"
                            },
                            {
                                name: "São Lourenço Airport",
                                address: "BR, Brasil (SSO)",
                                type: "airport"
                            },
                            {
                                name: "Aeroporto Internacional de Guarulhos",
                                address: "BR, Brasil (GRU)",
                                type: "airport"
                            }
                        ]}
                    />
                </Grid>
            </Paper>
        </MuiThemeProvider>

    );
};

const argsControls = {
    args: {
        brand: "CVC",
        mode: "root",
    },
    argTypes: {
        brand: {
            type: "select",
            options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
        },
        mode: {
            type: "select",
            options: ["root", "darkBg"]
        },
    }
}

storiesOf("Components/Pesqinc/Hotel", module)
    .add("Default", (args) => <PesqincHotelStory {...args}/>, argsControls);
